body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*! minireset.css v0.0.3 | MIT License | github.com/jgthms/minireset.css */
html, body, p, ol, ul, li, dl, dt, dd, blockquote, figure, fieldset, legend, textarea, pre, iframe, hr, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button, input, select, textarea {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

img, embed, iframe, object, audio, video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td, th {
  padding: 0;
  text-align: left;
}

html {
  background-color: #5f45bb;
  background-image: linear-gradient(to bottom right, #180cac, #d054e4);
  color: #ffffff;
  font-family: 'Quicksand', sans-serif;
  font-size: 16px;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  min-height: 100vh;
/*   min-width: 300px; */
  overflow-x: hidden;
}


#root {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 50vh;
}


.screen {
  -webkit-align-items: center;
          align-items: center;
  background-color: white;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
  font-size: x-large;
  height: 50px;
  padding-right: 15px;
  text-align: end;
  width: 270px;
}
.keypad {
  -webkit-align-items: stretch;
          align-items: stretch;
  box-shadow: 0 1.5rem 3rem rgba(200, 0, 245, 0.15);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  height: 230px;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  width: 270px;
}

.keypad div {
  display: -webkit-flex;
  display: flex;
  height: 46px;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.keypad div:last-child button {
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}

button {
  background-color: #f98635;
  border: none;
  color: currentColor;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  text-decoration: none;
}

.button:active {
  -webkit-transform: scale(0.98) translateY(-2px);
          transform: scale(0.98) translateY(-2px);
}

.button:hover {
  -webkit-transform: scale(1.02) translateY(-4px);
          transform: scale(1.02) translateY(-4px);
} 
